import React, { useState, useEffect } from "react";
import { PieChart, Pie, Cell, Tooltip, Legend } from "recharts";
import Edit from "./edit";
import { Link } from "react-router-dom"
import { He } from "../tools/maths";

const MathFunctionButton = ({ idx, opt, img }) => {
  return (<><He left maxheight={100} imageUrl={img} he hein={`${String.fromCharCode(65 + idx)}) `} input={opt} /></>)
}

const MathFunctionText = ({ ques, img }) => {
  return (<><He input={ques} imageUrl={img} /></>)
}


export default function TestAnalysisWindow({ download, userForm, setUserForm, uid, time }) {
  const [tab, setTab] = useState("overview");
  const [animated, setAnimated] = useState(true);
  const [selectedIndex, setSelectedIndex] = useState(null);
  const [isFullScreen, setIsFullScreen] = useState(false);
  const [drawerOpen, setDrawerOpen] = useState(false);
  const [currentList, setCurrentList] = useState(userForm.map((q, i) => ({ ...q, originalIndex: i })));
  const [currentMode, setCurrentMode] = useState("all");
  const [edit, setedit] = useState(false);

  useEffect(() => {
    const timer = setTimeout(() => setAnimated(false), 600);
    return () => clearTimeout(timer);
  }, []);

  const getQuestionColor = (q) => {
    // For Other category (invalid answerKey)
    const isAnswerKeyInvalid =
      !Array.isArray(q.answerKey) ||
      q.answerKey.length === 0 ||
      !q.answerKey.every(num => Number.isInteger(num));

    if (isAnswerKeyInvalid) return "bg-blue-200";

    if (q.chosen.length === 0) return "bg-gray-300";
    const chosenValue = q.type === "int" ? q.chosen[0] : [...q.chosen].sort().join();
    const answerValue = q.type === "int" ? q.answerKey[0] : [...q.answerKey].sort().join();
    return chosenValue === answerValue ? "bg-green-400 text-white" : "bg-red-400 text-white";
  };

  const analysis = {
    total: userForm.length,
    correct: userForm.filter(q =>
      q.type === "int"
        ? q.chosen[0] === q.answerKey[0]
        : ([...q.chosen]).sort().join() === ([...q.answerKey]).sort().join()
    ).length,
    unattempted: userForm.filter(q => q.chosen.length === 0).length,
    other: userForm.filter(q =>
      !Array.isArray(q.answerKey) ||
      q.answerKey.length === 0 ||
      !q.answerKey.every(num => Number.isInteger(num))
    ).length
  };
  analysis.incorrect = analysis.total - analysis.correct - analysis.unattempted - analysis.other;

  const chartData = [
    { name: "Correct", value: analysis.correct },
    { name: "Incorrect", value: analysis.incorrect },
    { name: "Unattempted", value: analysis.unattempted },
    { name: "Other", value: analysis.other }
  ];
  const COLORS = ["#34D399", "#F87171", "#D1D5DB", "lightblue"];

  const datadash = [{ name: "Questions", value: 100 }];
  const COLORSDASH = ["#1a63a7ff"];

  const filteredQuestions = (type) => {
    const withIndex = userForm.map((q, i) => ({ ...q, originalIndex: i }));
    return withIndex.filter(q => {
      const correct = q.type === "int"
        ? q.chosen[0] === q.answerKey[0]
        : ([...q.chosen]).sort().join() === ([...q.answerKey]).sort().join();

      const isAnswerKeyInvalid =
        !Array.isArray(q.answerKey) ||
        q.answerKey.length === 0 ||
        !q.answerKey.every(num => Number.isInteger(num));

      if (type === "correct") return correct;
      if (type === "incorrect") return q.chosen.length > 0 && !correct && !isAnswerKeyInvalid;
      if (type === "unattempted") return q.chosen.length === 0 && !isAnswerKeyInvalid;
      if (type === "other") return isAnswerKeyInvalid;
      return true;
    });
  };

  const openQuestion = (index, mode) => {
    const list = filteredQuestions(mode);
    setCurrentList(list);
    setSelectedIndex(index);
    setCurrentMode(mode);
  };

  const selectedQuestion = selectedIndex !== null ? currentList[selectedIndex] : null;

  function SubmitEdit(e) {
    try {
      let tests = JSON.parse(localStorage.getItem("tests")) || [];
      if (!Array.isArray(tests)) throw new Error("Tests data is not an array");

      const index = tests.findIndex(test => test.uid === uid);
      if (index !== -1) {
        tests[index].questions = e;
      } else {
        console.warn(`Test with uid ${uid} not found`);
      }

      localStorage.setItem("tests", JSON.stringify(tests));
      setUserForm(e); // sync state after edit
    } catch (error) {
      console.error("Error updating test:", error);
    }
  }

  function subtractTimes(time1, time2) {
    const toSeconds = (time) => {
      const [m, s] = time.split(":").map(Number);
      return m * 60 + s;
    };

    const toMMSS = (seconds) => {
      const m = Math.floor(seconds / 60);
      const s = seconds % 60;
      return `${String(m).padStart(2, "0")}:${String(s).padStart(2, "0")}`;
    };

    let diff = toSeconds(time1) - toSeconds(time2);
    if (diff < 0) diff = 0; // prevent negative time

    return toMMSS(diff);
  }


  const [showDownloadModal, setShowDownloadModal] = useState(false);
  const [modalStage, setModalStage] = useState("disclaimer"); // disclaimer | success


  function removeChosen(arr) {
    return arr.map(({ chosen, ...rest }) => rest);
  }

  function removeTime(obj) {
    const copy = { ...obj };
    delete copy.timeleft;
    return copy;
  }

  function generateHexCode() {
  return "#" + Math.floor(Math.random() * 16777215).toString(16).padStart(6, "0");
}
  const handleDownload = () => {
    let hi = download;
    hi.status = "true"
    let his = removeTime(hi)
    his.uid = `${his.uid}-${generateHexCode()}`
    his.questions = (removeChosen(his.questions))


    // hi.questions = removeChosen(download.question)
    const jsonData = JSON.stringify([his], null, 2);
    const blob = new Blob([jsonData], { type: "application/json" });
    const url = URL.createObjectURL(blob);
    const a = document.createElement("a");
    a.href = url;
    a.download = "test.json";
    document.body.appendChild(a);
    a.click();
    document.body.removeChild(a);
    URL.revokeObjectURL(url);
    setModalStage("success");
  };

  const handleCopy = () => {
    navigator.clipboard.writeText(JSON.stringify(removeChosen(download.questions), null, 2)).then(() => {
      setModalStage("success");
    });
  };

  return (
    <>

      {showDownloadModal && (
        <div className="hehe fixed inset-0 bg-black/40 backdrop-blur-sm flex justify-center items-center z-50">
          <div className="bg-white/90 backdrop-blur-lg p-6 rounded-2xl shadow-2xl w-[320px] border border-gray-200 animate-fadeSlide">

            {modalStage === "disclaimer" && (
              <>
                {/* Header */}
                <div className="flex justify-between items-center mb-4">
                  <h3 className="font-bold text-lg text-gray-800">📂 Download Data</h3>
                  <button
                    onClick={() => setShowDownloadModal(false)}
                    className="hover:bg-gray-200 p-1 rounded-full transition"
                  >
                    ✖
                  </button>
                </div>

                {/* Disclaimer */}
                <p className="text-sm mb-3 text-gray-600">
                  By downloading, the JSON can be used in{" "}
                  <Link
                    className="text-indigo-500 font-medium hover:text-indigo-600 hover:underline underline-offset-2 transition-colors duration-200"
                    to={"/upload-json"}
                  >
                    upload section
                  </Link>.
                </p>
                <p className="text-sm mb-4 text-gray-600">
                  By copying, you can paste it in the{" "}
                  <Link
                    className="text-indigo-500 font-medium hover:text-indigo-600 hover:underline underline-offset-2 transition-colors duration-200"
                    to={"/create-test"}
                  >
                    create test
                  </Link> Data (JSON) input field (for reattempt).
                </p>

                {/* Buttons */}
                <div className="flex flex-col gap-3">
                  <button
                    onClick={handleDownload}
                    className="bg-gradient-to-r from-blue-500 to-indigo-500 text-white p-2 rounded-lg shadow-md hover:shadow-lg hover:from-blue-600 hover:to-indigo-600 transition-all"
                  >
                    ⬇ Download JSON
                  </button>
                  <button
                    onClick={handleCopy}
                    className="bg-gradient-to-r from-green-500 to-emerald-500 text-white p-2 rounded-lg shadow-md hover:shadow-lg hover:from-green-600 hover:to-emerald-600 transition-all"
                  >
                    📋 Copy Only Questions
                  </button>
                </div>
              </>
            )}

            {modalStage === "success" && (
              <div className="text-center">
                <h3 className="font-bold text-lg mb-4 text-green-600">Success</h3>
                <button
                  onClick={() => setShowDownloadModal(false)}
                  className="bg-gradient-to-r from-indigo-500 to-purple-500 text-white p-2 rounded-lg w-full shadow-md hover:shadow-lg hover:from-indigo-600 hover:to-purple-600 transition-all"
                >
                  Close
                </button>
              </div>
            )}
          </div>
        </div>

      )}
      {edit === false ? (
        <div className="fixed inset-0 bg-gray-100 z-50 overflow-auto flex justify-center items-start p-6">
          <div className="flex w-full max-w-5xl bg-white rounded-xl shadow-xl overflow-hidden min-h-[60vh] max-h-[90vh] border border-gray-200 relative">
            {/* Main Content */}
            <div className="flex-1 flex flex-col p-4 md:p-6">
              <div className="flex justify-between items-center p-4 border-b border-gray-200 bg-gray-50 sticky top-0 z-10 shadow-sm rounded-t-xl">
                <h2 className="text-lg md:text-2xl font-bold text-gray-800">📊 <span className="max-sm:hidden">Test</span> Analysis</h2>
                <div className="flex gap-2">
                  <button onClick={() => { setShowDownloadModal(true); setModalStage("disclaimer") }} className="p-2 rounded-full hover:bg-gray-200">
                    <svg xmlns="http://www.w3.org/2000/svg"
                      fill="none" viewBox="0 0 24 24"
                      strokeWidth={2} stroke="currentColor"
                      className="w-5 h-5 md:w-6 md:h-6 text-gray-600">
                      <path strokeLinecap="round" strokeLinejoin="round"
                        d="M4 16v2a2 2 0 002 2h12a2 2 0 002-2v-2M7 10l5 5m0 0l5-5m-5 5V4" />
                    </svg>

                  </button>
                  <button onClick={() => setDrawerOpen(!drawerOpen)} className="md:hidden p-2 rounded-full hover:bg-gray-200">
                    <svg xmlns="http://www.w3.org/2000/svg" fill="none" viewBox="0 0 24 24" strokeWidth={2} stroke="currentColor" className="w-5 h-5">
                      <path strokeLinecap="round" strokeLinejoin="round" d="M4 6h16M4 12h16M4 18h16" />
                    </svg>
                  </button>
                  <Link to={"/"} className="p-2 rounded-full hover:bg-gray-200">
                    <svg xmlns="http://www.w3.org/2000/svg" fill="none" viewBox="0 0 24 24" strokeWidth={2} stroke="currentColor" className="w-5 h-5 md:w-6 md:h-6 text-gray-600">
                      <path strokeLinecap="round" strokeLinejoin="round" d="M6 18L18 6M6 6l12 12" />
                    </svg>
                  </Link>
                  <button onClick={() => { setedit(true) }} className="p-2 rounded-full hover:bg-gray-200">
                    <svg className="w-6 h-6 text-gray-700" fill="currentColor" viewBox="0 0 20 20">
                      <path d="M13.586 3.586a2 2 0 112.828 2.828l-9.9 9.9a2 2 0 01-.707.465l-4 1a1 1 0 01-1.273-1.273l1-4a2 2 0 01.465-.707l9.9-9.9z" />
                    </svg>
                  </button>
                </div>
              </div>

              <div className="mb-4 flex flex-wrap gap-2 md:gap-4 border-b border-gray-200 px-2 md:px-4 py-2">
                {"overview all correct incorrect unattempted other".split(" ").map(t => (
                  <button key={t} onClick={() => setTab(t)}
                    className={`px-2 md:px-4 py-1 md:py-2 text-sm md:text-base ${tab === t ? 'border-b-2 border-indigo-500 text-indigo-500' : 'text-gray-500'}`}>{t.toUpperCase()}</button>
                ))}
              </div>

              <div className="overflow-y-auto flex-1">
                {tab === 'overview' && (
                  analysis.total === 0 ?
                    <div className="flex flex-col items-center p-4">
                      <PieChart width={250} height={250} className="md:w-[300px] md:h-[300px]">
                        <Pie data={datadash} cx="50%" cy="50%" dataKey="value"
                          isAnimationActive={animated} animationDuration={500} animationEasing="ease-out">
                          {datadash.map((entry, index) => (
                            <Cell key={`cell-${index}`} fill={COLORSDASH[index]} />
                          ))}
                        </Pie>
                        <Tooltip contentStyle={{ backgroundColor: "#fff", borderRadius: "8px" }} formatter={() => 0} />
                        <Legend />
                      </PieChart>
                      <div className="mt-2 flex items-center gap-6 px-4 py-2 bg-gray-100 rounded-lg shadow-sm text-gray-800">
                        <div className="flex items-center gap-2">
                          <span className="text-blue-500 font-semibold">Duration:</span>
                          <span>{time.start}</span>
                        </div>
                        <div className="flex items-center gap-2">
                          <span className="text-green-500 font-semibold">Used:</span>
                          <span>{subtractTimes(time.start, time.left ? time.left : time.start)}</span>
                        </div>
                      </div>

                    </div> :
                    <div className="flex flex-col items-center p-4">
                      <PieChart width={250} height={250} className="md:w-[300px] md:h-[300px]">
                        <Pie isAnimationActive={animated} animationDuration={500} animationEasing="ease-out" data={chartData} cx="50%" cy="50%" outerRadius={80} dataKey="value">
                          {chartData.map((entry, index) => (
                            <Cell key={`cell-${index}`} fill={COLORS[index % COLORS.length]} />
                          ))}
                        </Pie>
                        <Tooltip />
                        <Legend />
                      </PieChart>
                      <div className="mt-2 flex items-center gap-6 px-4 py-2 bg-gray-100 rounded-lg shadow-sm text-gray-800">
                        <div className="flex items-center gap-2">
                          <span className="text-blue-500 font-semibold">Duration:</span>
                          <span>{time.start}</span>
                        </div>
                        <div className="flex items-center gap-2">
                          <span className="text-green-500 font-semibold">Used:</span>
                          <span>{subtractTimes(time.start, time.left ? time.left : time.start)}</span>
                        </div>
                      </div>

                    </div>
                )}

                {["all", "correct", "incorrect", "unattempted", "other"].includes(tab) && (
                  <div className="flex gap-3 p-4 max-h-[55vh] overflow-y-auto">
                    {filteredQuestions(tab).map((q, i) => (
                      <div onClick={() => openQuestion(i, tab)} key={i}
                        className={`p-3 min-w-20 text-center md:p-4 rounded-lg border border-gray-200 text-sm md:text-base transition-transform transform hover:scale-[1.02] hover:shadow-md cursor-pointer ${getQuestionColor(q)}`}>
                        {q.originalIndex + 1}
                      </div>
                    ))}
                  </div>
                )}
              </div>
            </div>

            {/* Sidebar for Desktop */}
            <div className="hidden md:flex flex-[0.3] bg-gray-50 p-4 overflow-y-auto border-l border-gray-200 flex-col items-center">
              <h3 className="text-base md:text-lg font-bold mb-4 text-center">Questions</h3>
              <div className="grid grid-cols-2 lg:grid-cols-3 gap-3 justify-center">
                {userForm.map((q, i) => (
                  <div key={i} onClick={() => openQuestion(i, "all")}
                    className={`w-10 h-10 flex items-center justify-center rounded cursor-pointer text-xs font-bold ${getQuestionColor(q)}`}>
                    {i + 1}
                  </div>
                ))}
              </div>
            </div>

            {/* Drawer for Mobile */}
            <div className={`hehe fixed bottom-0 left-0 w-full bg-white shadow-lg border-t border-gray-200 md:hidden p-4 transform transition-transform duration-300 ${drawerOpen ? 'translate-y-0' : 'translate-y-full'}`}>
              <h3 className="text-base font-bold mb-3 text-center">Questions</h3>
              <div className="grid grid-cols-8 gap-2 justify-center">
                {userForm.map((q, i) => (
                  <div key={i} onClick={() => openQuestion(i, "all")}
                    className={`w-8 h-8 flex items-center justify-center rounded cursor-pointer text-xs font-bold ${getQuestionColor(q)}`}>
                    {i + 1}
                  </div>
                ))}
              </div>
            </div>
          </div>

          {selectedQuestion && (
            <div className="fixed inset-0 bg-black/40 backdrop-blur-sm flex justify-center items-center z-50 px-2 sm:px-4">
              <div
                className={`bg-white/90 backdrop-blur-xl rounded-2xl shadow-2xl flex flex-col transition-all duration-500 ease-in-out transform
        ${isFullScreen ? "w-[95vw] h-[90vh]" : "w-full max-w-3xl max-h-[85vh]"} 
        border border-gray-200 overflow-hidden`}
              >

                {/* HEADER */}
                <div className="relative flex flex-col flex-shrink-0">
                  <div className="flex justify-between items-center bg-gradient-to-r from-indigo-500 to-purple-500 text-white px-4 py-3 shadow-md">
                    <h3 className="text-lg sm:text-xl font-bold drop-shadow-sm">
                      [{currentMode.toUpperCase()}] Q{selectedQuestion.originalIndex + 1}
                    </h3>
                    <div className="flex gap-2">
                      {/* Fullscreen toggle */}
                      <button
                        onClick={() => setIsFullScreen(!isFullScreen)}
                        className="hover:bg-white/20 rounded-full p-1 transition-colors"
                      >
                        <svg xmlns="http://www.w3.org/2000/svg"
                          viewBox="0 0 24 24"
                          fill="none"
                          strokeWidth={2}
                          stroke="currentColor"
                          className="w-5 h-5">
                          <path strokeLinecap="round" strokeLinejoin="round"
                            d="M4 8V4h4M20 8V4h-4M4 16v4h4M20 16v4h-4" />
                        </svg>
                      </button>
                      {/* Close */}
                      <button
                        onClick={() => setSelectedIndex(null)}
                        className="hover:bg-white/20 rounded-full p-1 transition-colors"
                      >
                        <svg xmlns="http://www.w3.org/2000/svg"
                          viewBox="0 0 24 24"
                          fill="none"
                          strokeWidth={2}
                          stroke="currentColor"
                          className="w-5 h-5">
                          <path strokeLinecap="round" strokeLinejoin="round"
                            d="M6 18L18 6M6 6l12 12" />
                        </svg>
                      </button>
                    </div>
                  </div>

                  {/* Progress bar */}
                  <div className="h-1 w-full bg-white/30">
                    <div
                      className="h-full bg-green-400 transition-all duration-500 ease-out"
                      style={{
                        width: `${((selectedIndex + 1) / currentList.length) * 100}%`,
                      }}
                    />
                  </div>
                </div>

                {/* SCROLLABLE BODY */}
                <div
                  key={selectedQuestion.originalIndex} // key for animation
                  className="flex-1 overflow-auto p-6 bg-gradient-to-b from-gray-50 to-gray-100 animate-fadeSlide"
                >
                  <p className="mb-4 font-medium text-gray-800 text-lg leading-relaxed">
                    <MathFunctionText img={selectedQuestion.img ? selectedQuestion.img !== "" ? selectedQuestion.img : undefined : undefined} ques={selectedQuestion.question} maxheight={300} />
                  </p>

                  {/* OPTIONS */}
                  <div className="space-y-3 mb-6">
                    {selectedQuestion.type !== "int" ? (
                      selectedQuestion.options.map((opt, idx) => (
                        <div
                          key={idx}
                          className={`p-3 rounded-xl border text-base transition-all cursor-pointer shadow-sm
                  ${Array.isArray(selectedQuestion.answerKey) &&
                              selectedQuestion.answerKey.includes(idx)
                              ? "bg-green-100 border-green-300 hover:shadow-md"
                              : selectedQuestion.chosen.includes(idx)
                                ? "bg-red-100 border-red-300 hover:shadow-md"
                                : "bg-white hover:bg-indigo-50 border-gray-200 hover:shadow-md"
                            }`}
                        >
                          <MathFunctionButton img={undefined} idx={idx} opt={opt} />
                        </div>
                      ))
                    ) : (
                      <div className="flex flex-col sm:flex-row gap-4">
                        <div className="flex-1 p-4 border border-gray-300 rounded-lg bg-gray-50 shadow-inner">
                          {Array.isArray(selectedQuestion.answerKey)
                            ? selectedQuestion.answerKey[0]
                            : "Invalid"}
                        </div>
                        <div
                          className={`flex-1 p-4 border border-gray-300 rounded-lg shadow-inner
                  ${Array.isArray(selectedQuestion.answerKey) &&
                              selectedQuestion.answerKey[0] == selectedQuestion.chosen[0]
                              ? "bg-green-100"
                              : "bg-red-100"
                            }`}
                        >
                          {selectedQuestion.chosen ? selectedQuestion.chosen[0] : ""}
                        </div>
                      </div>
                    )}
                  </div>
                </div>

                {/* FOOTER */}
                <div className="flex justify-between items-center bg-white/80 backdrop-blur-sm border-t border-gray-300 px-4 py-3 flex-shrink-0 shadow-lg">
                  <button
                    disabled={selectedIndex === 0}
                    onClick={() => setSelectedIndex((prev) => prev - 1)}
                    className="px-4 py-2 rounded-lg bg-gradient-to-r from-gray-200 to-gray-300 hover:from-gray-300 hover:to-gray-400 disabled:opacity-50 transition-all shadow-md hover:shadow-lg"
                  >
                    ⬅ Prev
                  </button>
                  <button
                    disabled={selectedIndex === currentList.length - 1}
                    onClick={() => setSelectedIndex((prev) => prev + 1)}
                    className="px-4 py-2 rounded-lg bg-gradient-to-r from-indigo-400 to-purple-500 hover:from-indigo-500 hover:to-purple-600 text-white disabled:opacity-50 transition-all shadow-md hover:shadow-lg"
                  >
                    Next ➡
                  </button>
                </div>
              </div>
            </div>
          )}

        </div>
      ) : (
        <div className="fixed inset-0 bg-gray-100 z-50 overflow-auto flex justify-center items-start p-6">
          <Edit setedit={setedit} onSubmit={(e) => { SubmitEdit(e) }} userData={userForm} setUserData={setUserForm} />
        </div>
      )}
    </>
  );
}
