import React, { useState } from "react";
import { useNavigate } from "react-router-dom";

export default function UploadFomo() {


  const getBaseName = () => {
    if (window.location.hostname.includes("github.io")) {
      // Automatically use repo name for GitHub Pages
      return "/test-build"; // Change to your repo name
    }
    return ""; // No basename for localhost or custom domain
  };
  const navigate = useNavigate();

  // 🔑 Owner password
  const OWNER_PASSWORD = "brahma";

  const [modalStep, setModalStep] = useState(0); // 0 = none, 0.5 = password, 1 = confirmation, 2 = checks, 3 = success
  const [currentCheck, setCurrentCheck] = useState("");
  const [getRequestPassed, setGetRequestPassed] = useState(null);
  const [updatingPassed, setUpdatingPassed] = useState(null);
  const [errorMessage, setErrorMessage] = useState("");
  const [redirectTimer, setRedirectTimer] = useState(5);

  // Password state
  const [password, setPassword] = useState("");
  const [passwordError, setPasswordError] = useState("");

  const openPasswordPrompt = () => {
    setModalStep(0.5); // Open password modal
  };

  const verifyPassword = () => {
    if (password === OWNER_PASSWORD) {
      setPasswordError("");
      setModalStep(1); // Move to confirmation modal
    } else {
      setPasswordError("Incorrect password. Try again.");
    }
  };

  const startChecks = async () => {
    setModalStep(2);
    setCurrentCheck("get");
    setGetRequestPassed(null);
    setUpdatingPassed(null);
    setErrorMessage("");

    try {
      // Perform GET Request
      const response = await fetch(`${window.location.origin}${getBaseName()}/fomo.json`);
      if (!response.ok) throw new Error("GET Request failed");

      let data;
      try {
        data = await response.json();
        if (!Array.isArray(data)) throw new Error("Not an array");
      } catch (jsonError) {
        throw new Error("Invalid JSON format");
      }

      // ✅ GET passed
      setGetRequestPassed(true);

      // Start Updating
      setCurrentCheck("updating");

      setTimeout(() => {
        try {
          // Clear LocalStorage
          localStorage.removeItem("time");
          localStorage.removeItem("userData");
          localStorage.removeItem("tests");

          // Save tests in LocalStorage
          localStorage.setItem("tests", JSON.stringify(data));

          // Create time array
          const timeArray = data.map(obj => ({
            id: obj.uid,
            timeleft: obj.duration,
          }));
          localStorage.setItem("time", JSON.stringify(timeArray));

          // ✅ Updating passed
          setUpdatingPassed(true);
          setCurrentCheck("");

          // Show success modal
          setTimeout(() => {
            setModalStep(3);
            // Auto redirect in 5 seconds
            let timer = 5;
            const countdown = setInterval(() => {
              timer--;
              setRedirectTimer(timer);
              if (timer <= 0) {
                clearInterval(countdown);
                navigate("/");
              }
            }, 1000);
          }, 800);
        } catch (updateError) {
          console.error("Updating failed:", updateError);
          setUpdatingPassed(false);
        }
      }, 1500);
    } catch (error) {
      console.error("Error during GET check:", error);
      setGetRequestPassed(false);
      setCurrentCheck("");
      setErrorMessage("Our developer is Useless");
    }
  };

  const CheckIcon = ({ status }) => {
    if (status === true)
      return (
        <svg
          className="w-5 h-5 text-green-400"
          fill="none"
          stroke="currentColor"
          strokeWidth="3"
          viewBox="0 0 24 24"
        >
          <path strokeLinecap="round" strokeLinejoin="round" d="M5 13l4 4L19 7" />
        </svg>
      );
    if (status === false)
      return (
        <svg
          className="w-5 h-5 text-red-400"
          fill="none"
          stroke="currentColor"
          strokeWidth="3"
          viewBox="0 0 24 24"
        >
          <path strokeLinecap="round" strokeLinejoin="round" d="M6 18L18 6M6 6l12 12" />
        </svg>
      );
    return <span className="w-3 h-3 rounded-full bg-blue-400 animate-pulse"></span>;
  };

  const LoadingSpinner = () => (
    <svg className="animate-spin h-5 w-5 text-blue-400" viewBox="0 0 24 24">
      <circle
        className="opacity-25"
        cx="12"
        cy="12"
        r="10"
        stroke="currentColor"
        strokeWidth="4"
      ></circle>
      <path
        className="opacity-75"
        fill="currentColor"
        d="M4 12a8 8 0 018-8v8H4z"
      ></path>
    </svg>
  );

  return (
    <div className="relative flex flex-col items-center justify-center min-h-screen bg-gray-950 text-white overflow-hidden px-6 py-14">
      {/* Background Glow */}
      <div className="absolute top-0 left-0 w-[600px] h-[600px] bg-red-500/20 rounded-full blur-3xl animate-pulse"></div>
      <div className="absolute bottom-0 right-0 w-[600px] h-[600px] bg-orange-500/20 rounded-full blur-3xl animate-pulse"></div>

      {/* Card */}
      <div className="bg-gray-900 border border-red-500/30 shadow-lg rounded-2xl p-8 w-full max-w-lg relative z-10">
        <h1 className="text-3xl font-extrabold mb-4 text-transparent bg-gradient-to-r from-red-400 via-pink-400 to-orange-400 bg-clip-text">
          🔐 Owner Confirmation
        </h1>
        <p className="text-gray-300 text-sm mb-6">
          This section is restricted. Only the legitimate owner of this website is authorized to proceed.
          All existing data will be replaced entirely with the developer’s official dataset.
          Unauthorized actions may cause irreversible data loss or critical errors.
        </p>

        <div className="flex justify-end gap-4">
          <button
            onClick={() => navigate("/")}
            className="px-4 py-2 rounded-lg bg-gray-800 hover:bg-gray-700 text-gray-300 transition-all"
          >
            Cancel
          </button>
          <button
            onClick={openPasswordPrompt}
            className="px-4 py-2 rounded-lg bg-gradient-to-r from-red-500 to-orange-500 hover:from-red-400 hover:to-orange-400 shadow-lg shadow-red-500/50 text-white font-semibold transition-all hover:scale-105 active:scale-95"
          >
            Continue
          </button>
        </div>
      </div>

      {/* Modal Step 0.5: Password */}
      {modalStep === 0.5 && (
        <div className="fixed inset-0 z-50 flex items-center justify-center bg-black/60 backdrop-blur-md">
          <div className="bg-gray-900 border border-gray-700 rounded-2xl w-[400px] p-6 relative">
            <h2 className="text-xl font-bold text-red-400 mb-4">🔑 Enter Owner Password</h2>
            <input
              type="password"
              value={password}
              onChange={e => setPassword(e.target.value)}
              placeholder="Enter password"
              className="w-full px-4 py-2 mb-3 bg-gray-800 border border-gray-700 rounded-lg text-gray-300 focus:outline-none focus:ring-2 focus:ring-red-400"
            />
            {passwordError && <p className="text-red-400 text-sm mb-3">{passwordError}</p>}
            <div className="flex justify-end gap-4">
              <button
                onClick={() => setModalStep(0)}
                className="px-4 py-2 rounded-lg bg-gray-800 hover:bg-gray-700 text-gray-300 transition-all"
              >
                Cancel
              </button>
              <button
                onClick={verifyPassword}
                className="px-4 py-2 rounded-lg bg-gradient-to-r from-red-500 to-orange-500 text-white font-semibold transition-all hover:scale-105 active:scale-95"
              >
                Verify
              </button>
            </div>
          </div>
        </div>
      )}

      {/* Modal Step 1: Confirmation */}
      {modalStep === 1 && (
        <div className="fixed inset-0 z-50 flex flex-col items-center justify-center bg-black/60 backdrop-blur-md">
          <div className="bg-gray-900 border border-gray-700 rounded-2xl shadow-2xl w-[400px] p-6 relative">
            <h2 className="text-xl font-bold text-red-400 mb-4">!! Owner Access Confirmed</h2>
            <p className="text-gray-300 mb-6">
              You now have access to advanced upload actions. Proceed carefully, as changes will be irreversible.
            </p>
            <p className="text-red-400 text-sm font-semibold mb-6">
              Are you sure you want to replace all your tests? This process is not reversible.
            </p>
            <div className="flex justify-center gap-4">
              <button
                onClick={() => setModalStep(0)}
                className="px-4 py-2 rounded-lg bg-gray-800 hover:bg-gray-700 text-gray-300 transition-all"
              >
                Cancel
              </button>
              <button
                onClick={startChecks}
                className="px-4 py-2 rounded-lg bg-gradient-to-r from-red-500 to-orange-500 hover:from-red-400 hover:to-orange-400 shadow-lg shadow-red-500/50 text-white font-semibold transition-all hover:scale-105 active:scale-95"
              >
                Proceed
              </button>
            </div>
            <button
              onClick={() => setModalStep(0)}
              className="absolute top-3 right-3 text-red-400 hover:text-red-300 text-lg"
            >
              ✖
            </button>
          </div>
        </div>
      )}

      {/* Modal Step 2: Checks */}
      {modalStep === 2 && (
        <div className="fixed inset-0 z-50 flex flex-col items-center justify-center bg-black/60 backdrop-blur-md">
          <div className="bg-gray-900 border border-gray-700 rounded-2xl shadow-2xl w-[400px] p-6 relative">
            <h2 className="text-xl font-bold text-red-400 mb-4">⚠ Owner Access Confirmed</h2>
            <p className="text-gray-300 mb-6">
              You now have access to advanced upload actions. Proceed carefully, as changes will be irreversible.
            </p>
            <div className="space-y-4">
              <div className="flex items-center gap-3">
                {currentCheck === "get" ? <LoadingSpinner /> : <CheckIcon status={getRequestPassed} />}
                <span
                  className={`${getRequestPassed === true
                      ? "text-green-400"
                      : getRequestPassed === false
                        ? "text-red-400"
                        : "text-blue-400"
                    }`}
                >
                  GET Request
                </span>
              </div>
              <div className="flex items-center gap-3">
                {currentCheck === "updating" ? <LoadingSpinner /> : <CheckIcon status={updatingPassed} />}
                <span
                  className={`${updatingPassed === true
                      ? "text-green-400"
                      : updatingPassed === false
                        ? "text-red-400"
                        : "text-blue-400"
                    }`}
                >
                  Updating
                </span>
              </div>
            </div>

            {/* Error message */}
            {errorMessage && (
              <div className="mt-6 text-center">
                <p className="text-red-400 font-semibold">{errorMessage}</p>
                <button
                  onClick={() => navigate("/")}
                  className="mt-4 px-4 py-2 rounded-lg bg-gray-800 hover:bg-gray-700 text-gray-300 transition-all"
                >
                  Close
                </button>
              </div>
            )}

            <button
              onClick={() => setModalStep(0)}
              className="absolute top-3 right-3 text-red-400 hover:text-red-300 text-lg"
            >
              ✖
            </button>
          </div>
        </div>
      )}

      {/* Modal Step 3: Success */}
      {modalStep === 3 && (
        <div className="fixed inset-0 z-50 flex flex-col items-center justify-center bg-black/60 backdrop-blur-md">
          <div className="bg-gray-900 border border-green-500/50 rounded-2xl shadow-2xl w-[400px] p-6 relative text-center">
            <h2 className="text-xl font-bold text-green-400 mb-4">✅ Successfully Updated</h2>
            <p className="text-gray-300 mb-6">
              Your tests have been replaced successfully.
            </p>
            <p className="text-gray-400 text-sm mb-4">
              Redirecting to Home in {redirectTimer} seconds...
            </p>
            <button
              onClick={() => navigate("/")}
              className="px-4 py-2 rounded-lg bg-green-600 hover:bg-green-500 text-white font-semibold transition-all"
            >
              Go to Home
            </button>
          </div>
        </div>
      )}
    </div>
  );
}
