import React, { useState } from "react";
import { useNavigate, Link } from "react-router-dom";

export default function PlayingWithJson() {
  const [selectedPlan, setSelectedPlan] = useState(null);
  const [showModal, setShowModal] = useState(false);
  const [isChecked, setIsChecked] = useState(false);
  const navigate = useNavigate();

  const plans = [
    {
      id: "replace",
      title: "Replace",
      desc: "Replace all existing data with uploaded JSON.",
      glow: "shadow-red-500/40",
      selectedStyle: "bg-gradient-to-br from-red-900 via-red-700 to-pink-800 border-red-500 shadow-red-500/50",
      descs: ["Will delete All Your Data", "And replaces with your uploaded JSON"]
    },
    {
      id: "append",
      title: "Append",
      desc: "Append uploaded JSON data to the existing dataset.",
      glow: "shadow-green-400/40",
      selectedStyle: "bg-gradient-to-br from-green-900 via-green-700 to-emerald-800 border-green-400 shadow-green-400/50 scale-105",
      descs: ["Your data will be preserved and will add the tests in your data", "If any test's id of both your data and your JSON start conflicting then your test will be overwritten"]

    },
    {
      id: "fomo",
      title: "FOMO 😊❤️",
      desc: "Something mysterious… not that mysterious",
      glow: "shadow-purple-500/50",
      selectedStyle: "bg-gradient-to-br from-purple-900 via-pink-800 to-yellow-800 border-purple-400 shadow-purple-500/50 scale-105",
      descs: ["Only for the developer of this website", "To show how great or dumb is the developer"]

    }
  ];

  const handlePlanSelect = (id) => {
    setSelectedPlan(id);
  };

  const getAccentColor = () => {
    if (selectedPlan === "replace") return "red";
    if (selectedPlan === "append") return "green";
    if (selectedPlan === "fomo") return "purple";
    return "cyan";
  };

  const proceedAction = () => {
      navigate(`/upload/${selectedPlan}`);
  };

  return (
    <div className="relative flex flex-col items-center px-6 py-14 bg-gray-950 min-h-screen text-white overflow-hidden">

      {/* Animated Gradient Blobs */}
      <div className="absolute -top-40 -left-40 w-[800px] h-[800px] bg-purple-500/30 rounded-full blur-3xl animate-pulse"></div>
      <div className="absolute top-40 right-0 w-[600px] h-[600px] bg-pink-500/20 rounded-full blur-3xl animate-pulse"></div>
      <div className="absolute bottom-0 left-0 w-[700px] h-[700px] bg-cyan-500/20 rounded-full blur-3xl animate-ping"></div>

      {/* Title */}
      <h1 className="text-5xl font-extrabold mb-16 text-center tracking-tight bg-gradient-to-r from-cyan-400 via-purple-400 to-pink-400 bg-clip-text text-transparent drop-shadow-[0_0_15px_rgba(255,255,255,0.5)] animate-pulse">
        🎯 Playing with JSON
      </h1>

      {/* Cards */}
      <div className="grid md:grid-cols-3 gap-10 w-full max-w-6xl z-10">
        {plans.map((plan) => (
          <div
            key={plan.id}
            onClick={() => handlePlanSelect(plan.id)}
            className={`cursor-pointer rounded-2xl p-8 transition-all duration-500 transform backdrop-blur-lg border-2 
              bg-gray-900/60 hover:scale-105 hover:-translate-y-2 hover:shadow-[0_0_30px_rgba(255,255,255,0.2)] 
              ${plan.glow} 
              ${selectedPlan === plan.id ? plan.selectedStyle : "border-transparent"}
            `}
            style={{ transformStyle: "preserve-3d", perspective: "1000px" }}
          >
            <h3 className="text-2xl font-bold mb-3">{plan.title}</h3>
            <p className="text-gray-300 mb-6 text-sm">{plan.desc}</p>
            <ul className="space-y-2 text-gray-400 text-sm">
              {plan.descs ? plan.descs.map((e, i) => {
                return (              <li>• {e}</li>)
              }) :""}
            </ul>
          </div>
        ))}
      </div>

      {/* Action Button */}
      {selectedPlan && (
        <div className="mt-16 w-full max-w-xl text-center z-10">
          <button
            onClick={() => setShowModal(true)}
            className={`px-8 py-3 rounded-xl font-semibold text-white transition-all relative overflow-hidden group
              bg-${getAccentColor()}-500 hover:bg-${getAccentColor()}-600 shadow-lg`}
          >
            <span className="relative z-10">{selectedPlan.toUpperCase()}</span>
            <div className="absolute inset-0 bg-white/20 blur-xl opacity-0 group-hover:opacity-100 transition"></div>
          </button>
        </div>
      )}

      {/* Modal */}
      {showModal && (
        <div className="fixed inset-0 bg-black/70 backdrop-blur-sm flex justify-center items-center z-50 animate-fadeIn">
          <div className="bg-gray-900/80 backdrop-blur-lg rounded-2xl p-8 max-w-md w-full shadow-[0_0_30px_rgba(255,255,255,0.1)] border border-gray-700">
            <h2 className="text-2xl font-bold mb-4 text-red-400 animate-pulse">
              ⚠ Before We Proceed
            </h2>
            {selectedPlan === "fomo" ? (
              <p className="text-gray-300 mb-6">
                Make sure you are Shri Aayush Kumar Jha.
              </p>
            ) : (
              <>
                <p className="text-gray-300 mb-3">
                  Make sure the JSON is valid and has the required structure for the website to work.
                </p>
                <p className="text-gray-300 mb-6">
                  We will try our best to reduce errors, but invalid JSON can break functionality.
                </p>
              </>
            )}

            {/* Checkbox */}
            <div
              onClick={() => setIsChecked(!isChecked)}
              className="w-full mb-4 bg-gray-800/50 rounded-lg p-4 flex items-center gap-3 border border-gray-700 hover:border-gray-600 transition cursor-pointer select-none"
            >
              <div
                className={`w-6 h-6 flex items-center justify-center rounded-md border-2 transition-all
                  ${isChecked
                    ? `bg-${getAccentColor()}-500 border-${getAccentColor()}-400 shadow-[0_0_8px] shadow-${getAccentColor()}-500`
                    : "bg-gray-900 border-gray-500"
                  }
                `}
              >
                <svg
                  xmlns="http://www.w3.org/2000/svg"
                  className={`h-4 w-4 text-white transition-all duration-300 ${isChecked ? "opacity-100 scale-100" : "opacity-0 scale-75"}`}
                  fill="none"
                  viewBox="0 0 24 24"
                  stroke="currentColor"
                  strokeWidth={3}
                >
                  <path strokeLinecap="round" strokeLinejoin="round" d="M5 13l4 4L19 7" />
                </svg>
              </div>
              <span className="text-gray-300 text-sm leading-snug">
                {selectedPlan === "fomo"
                  ? " Yes, I am the developer."
                  : " Yes, I understand that if any error occurs then the localStorage might be cleared by the website, and in any website crash I will delete localStorage myself."}
              </span>
            </div>

            {/* Buttons */}
            <div className="flex justify-end gap-4">
              <button
                onClick={() => setShowModal(false)}
                className="px-4 py-2 rounded-lg bg-gray-700 hover:bg-gray-600"
              >
                Cancel
              </button>

              <Link
                to={`/upload/${selectedPlan}`}
                className={`px-4 py-2 rounded-lg transition-all text-center ${isChecked
                    ? `bg-${getAccentColor()}-500 hover:bg-${getAccentColor()}-600`
                    : "bg-gray-600 cursor-not-allowed pointer-events-none"
                  }`}
              >
                Proceed
              </Link>
            </div>

          </div>
        </div>
      )}
    </div>
  );
}
