import { useState } from "react";
import Editor from "react-simple-code-editor";
import Prism from "prismjs";
import "prismjs/components/prism-json";
import "prismjs/themes/prism-okaidia.css";
import "./css/index.css";
import { Link } from "react-router-dom";
import { v4 as uuidv4 } from 'uuid';

export default function CenteredForm({ setformData }) {
  const uniqueId = uuidv4();
  const [uid, setid] = useState("");
  const [testName, setTestName] = useState("");
  const [tags, setTags] = useState("");
  const [jsonData, setJsonData] = useState("");
  const [minutes, setMinutes] = useState("");
  const [status, setStatus] = useState("idle");
  const [jsonError, setJsonError] = useState("");

  const handleSubmit = (e) => {
    e.preventDefault();
    setStatus("submitting");

    const getRowColFromIndex = (jsonString, index) => {
      const before = jsonString.slice(0, index);
      const row = before.split("\n").length;
      const col = index - before.lastIndexOf("\n");
      return { row, col };
    };

    try {
      const parsedData = JSON.parse(jsonData);

      if (!Array.isArray(parsedData)) {
        throw new Error("Root JSON must be an array of questions");
      }

      // Validate time > 0
      if (!minutes || isNaN(minutes) || parseInt(minutes, 10) <= 0) {
        setJsonError("Duration must be greater than 0 minutes.");
        setStatus("idle");
        return;
      }

      parsedData.forEach((q, index) => {
        if (typeof q !== "object" || q === null || Array.isArray(q)) {
          throw new Error(`Question ${index + 1}: Must be an object`);
        }

        const allowedKeys = ["question", "type", "options", "answerKey", "img"];
        Object.keys(q).forEach((key) => {
          if (!allowedKeys.includes(key)) {
            const approxPos = jsonData.indexOf(`"${key}"`);
            const { row, col } = getRowColFromIndex(jsonData, approxPos);
            throw new Error(`Question ${index + 1}: Invalid key "${key}" (line ${row}, column ${col})`);
          }
        });

        if (typeof q.question !== "string") {
          const approxPos = jsonData.indexOf('"question"', jsonData.indexOf(`"question"`));
          const { row, col } = getRowColFromIndex(jsonData, approxPos);
          throw new Error(`Question ${index + 1}: "question" must be string (line ${row}, column ${col})`);
        }

        if (!["mcq", "int"].includes(q.type)) {
          const approxPos = jsonData.indexOf('"type"', jsonData.indexOf(`"type"`));
          const { row, col } = getRowColFromIndex(jsonData, approxPos);
          throw new Error(`Question ${index + 1}: "type" must be "mcq" or "int" (line ${row}, column ${col})`);
        }

        if (q.type === "mcq") {
          if (!Array.isArray(q.options) || !q.options.every(opt => typeof opt === "string")) {
            const approxPos = jsonData.indexOf('"options"', jsonData.indexOf(`"options"`));
            const { row, col } = getRowColFromIndex(jsonData, approxPos);
            throw new Error(`Question ${index + 1}: "options" must be array of strings (line ${row}, column ${col})`);
          }
        }

        if (!Array.isArray(q.answerKey) || !q.answerKey.every(num => Number.isInteger(num))) {
          const approxPos = jsonData.indexOf('"answerKey"', jsonData.indexOf(`"answerKey"`));
          const { row, col } = getRowColFromIndex(jsonData, approxPos);
          throw new Error(`Question ${index + 1}: "answerKey" must be array of integers (line ${row}, column ${col})`);
        }

        if (q.img !== undefined && typeof q.img !== "string") {
          const approxPos = jsonData.indexOf('"img"', jsonData.indexOf(`"img"`));
          const { row, col } = getRowColFromIndex(jsonData, approxPos);
          throw new Error(`Question ${index + 1}: "img" must be a string if provided (line ${row}, column ${col})`);
        }
      });

      const description = tags
        .split(",")
        .map(tag => tag.trim())
        .join(", ");
      let uid = uniqueId;

      const finalData = {
        uid,
        title: testName,
        description: description,
        questions: parsedData,
        duration: `${String(minutes).padStart(2, "0")}:00`,
        createdAt: new Date().toISOString(),
        status: "true",
      };

      let storedTests = [];
      try {
        const rawData = localStorage.getItem("tests");
        if (rawData) {
          const parsedTests = JSON.parse(rawData);
          if (Array.isArray(parsedTests)) {
            storedTests = parsedTests;
          } else {
            localStorage.setItem("tests", JSON.stringify([]));
          }
        }
      } catch {
        localStorage.setItem("tests", JSON.stringify([]));
      }

      storedTests.push(finalData);
      setid(uid);
      localStorage.setItem("tests", JSON.stringify(storedTests));

      let timeData = [];
      try {
        const rawTime = localStorage.getItem("time");
        if (rawTime) {
          const parsedTime = JSON.parse(rawTime);
          if (Array.isArray(parsedTime)) {
            timeData = parsedTime;
          } else {
            localStorage.setItem("time", JSON.stringify([]));
          }
        }
      } catch {
        localStorage.setItem("time", JSON.stringify([]));
      }

      const durationSeconds = parseInt(minutes, 10) * 60;
      const mm = String(Math.floor(durationSeconds / 60)).padStart(2, "0");
      const ss = String(durationSeconds % 60).padStart(2, "0");

      timeData = timeData.filter(entry => entry.id !== uid);
      timeData.push({ id: uid, timeleft: `${mm}:${ss}` });
      localStorage.setItem("time", JSON.stringify(timeData));

      setformData(finalData);
      setJsonError("");
      setTimeout(() => setStatus("success"), 800);

    } catch (err) {
      setJsonError(err.message);
      setStatus("idle");
    }
  };

  return (
    <div className="wrapper">
      <form onSubmit={handleSubmit} className="form-card">
        <h1 className="form-title">Create Test</h1>

        <div className="field">
          <label htmlFor="testName">Test Name</label>
          <input
            type="text"
            id="testName"
            placeholder="Enter test name..."
            value={testName}
            onChange={(e) => setTestName(e.target.value)}
            required
            className="input-field"
          />
        </div>

        <div className="field">
          <label htmlFor="tags">Tags (comma separated)</label>
          <input
            type="text"
            id="tags"
            placeholder="e.g., physics, mechanics, optics"
            value={tags}
            onChange={(e) => setTags(e.target.value)}
            className="input-field"
          />
        </div>

        <div className="field">
          <label>Data (JSON)</label>
          <div className="flex items-start gap-2">
            <div className={`flex-1 json-editor ${jsonError ? "error-border" : ""}`}>
              <div className="relative flex font-mono text-sm">
                <div
                  aria-hidden="true"
                  className="text-gray-500 select-none text-right pr-3 border-r border-gray-700"
                  style={{
                    paddingTop: "10px",
                    paddingBottom: "10px",
                    minWidth: "2rem",
                    background: "#272822",
                  }}
                >
                  {jsonData.split("\n").map((_, i) => (
                    <div key={i} className="leading-5">
                      {i + 1}
                    </div>
                  ))}
                </div>

                <div className="flex-1 overflow-x-auto">
                  <Editor
                    value={jsonData}
                    onValueChange={(code) => setJsonData(code)}
                    highlight={(code) => Prism.highlight(code, Prism.languages.json, "json")}
                    padding={10}
                    style={{
                      fontFamily: '"Fira Code", monospace',
                      fontSize: 14,
                      minHeight: "150px",
                      outline: "none",
                    }}
                  />
                </div>
              </div>
            </div>
            <div className="flex flex-col">
              <button
                type="button"
                onClick={() => {
                  try {
                    const parsed = JSON.parse(jsonData);
                    setJsonData(JSON.stringify(parsed, null, 2));
                    setJsonError("");
                  } catch (err) {
                    setJsonError("Invalid JSON: " + err.message);
                  }
                }}
                className="px-3 py-1 mt-1 rounded-lg bg-gradient-to-r from-blue-500 to-indigo-500 text-white text-sm hover:scale-105 active:scale-95 transition-transform"
              >
                Format
              </button>
              <button
                type="button"
                onClick={() => {
                  try {
                    const parsed = JSON.parse(jsonData);
                    setJsonData(JSON.stringify(parsed));
                    setJsonError("");
                  } catch (err) {
                    setJsonError("Invalid JSON: " + err.message);
                  }
                }}
                className="px-3 py-1 mt-1 rounded-lg bg-gradient-to-r from-blue-500 to-indigo-500 text-white text-sm hover:scale-105 active:scale-95 transition-transform"
              >
                Compact
              </button>
            </div>
          </div>
          {jsonError && <p className="error-text">{jsonError}</p>}
        </div>

{/* Duration */}
<div className="field">
  <label htmlFor="minutes">Duration (Minutes)</label>
  <input
    type="number"
    id="minutes"
    required
    min="1"
    step="1"
    value={minutes}
    onChange={(e) => {
      let val = e.target.value.replace(/^0+/, ""); // remove leading zeros
      if (val === "") val = "0"; // avoid empty string
      setMinutes(val);
    }}
    className="input-field"
    placeholder="e.g., 30"
  />
</div>

        <div className="actions flex flex-col gap-2 items-center">
          {status === "submitting" && (
            <div className="alert alert-success">⏳ Submitting...</div>
          )}
          {status === "success" && (
            <div className="alert alert-success">✅ Test Created Successfully!</div>
          )}
          {jsonError && (
            <div className="alert alert-error">❌ {jsonError}</div>
          )}

          <button type="submit" className="button-submit">
            {status === "submitting" ? "Creating..." : "Create Test"}
          </button>

          <Link
            to="/rules-json"
            className="inline-flex items-center gap-2 px-4 py-1.5 mt-3 rounded-full text-sm font-semibold text-white bg-gradient-to-r from-purple-500 via-pink-500 to-rose-500 shadow-md hover:shadow-lg hover:scale-105 active:scale-95 transition-all duration-300"
          >
            <span>📜</span>
            <span>Rules for JSON</span>
          </Link>
        </div>
      </form>

      {status === "success" && (
        <div className="fixed inset-0 z-50 flex items-center justify-center bg-black/60 backdrop-blur-sm">
          <div className="bg-slate-800 border border-slate-700 rounded-xl shadow-2xl w-96 p-6">
            <h2 className="text-2xl font-bold text-center bg-gradient-to-r from-blue-400 to-purple-400 bg-clip-text text-transparent mb-3">
              🎉 Test Created!
            </h2>
            <p className="text-slate-300 text-center mb-6">
              Do you want to take the test now or go back to home?
            </p>
            <div className="flex gap-3">
              <Link
                to={`/test/${uid}`}
                className="flex-1 py-2 rounded-lg bg-gradient-to-r from-blue-500 to-indigo-500 text-white text-center font-semibold hover:scale-105 active:scale-95 transition-transform"
              >
                Give Test Now
              </Link>
              <Link
                to="/"
                className="flex-1 py-2 rounded-lg bg-slate-700 text-slate-200 text-center font-semibold hover:bg-slate-600 hover:scale-105 active:scale-95 transition-transform"
              >
                Go to Home
              </Link>
            </div>
          </div>
        </div>
      )}
    </div>
  );
}
