import React, { useState, useEffect } from "react";
import { He } from "../tools/maths";

export default function Edit({userData, setedit, setUserData, onSubmit}) {
  const questions = userData

  const [qNo, setQNo] = useState(0);
  const [showModal, setShowModal] = useState(false);
  const [showAnalysis, setShowAnalysis] = useState(false);
  const [drawerOpen, setDrawerOpen] = useState(false);
  const [markedQuestions, setMarkedQuestions] = useState([]);
  const [userForm, setUserForm] = useState(questions.map((q, index) => ({ id: Number(index), answerKey: q.type === "int" ? [] : [], locked: false, ...q })));

  const toggleMarkQuestionFalse = (id) => setMarkedQuestions(prev => prev.includes(id) ? prev.filter(m => m !== id) : "");
  // const toggleChoice = (q, optionIndex) => q.answerKey.length === 1 ? [optionIndex] : q.answerKey.includes(optionIndex) ? q.answerKey.filter(i => i !== optionIndex) : [...q.answerKey, optionIndex];
  const toggleChoice = (q, optionIndex) => true === false ? [optionIndex] : q.answerKey.includes(optionIndex) ? q.answerKey.filter(i => i !== optionIndex) : [...q.answerKey, optionIndex];
  const handleOptionClick = (qId, optionIndex) => {toggleMarkQuestionFalse(qId); setUserForm(prevForm => prevForm.map(q => q.id === qId ? { ...q, answerKey: toggleChoice(q, optionIndex) } : q))};
  const handleIntChange = (qId, value) =>{toggleMarkQuestionFalse(qId); setUserForm(prevForm => prevForm.map(q => q.id === qId ? { ...q, answerKey: value ? [parseInt(value)] : [] } : q))};
  const handleIntConfirm = (qId) => {toggleMarkQuestionFalse(qId); setUserForm(prevForm => prevForm.map(q => q.id === qId ? { ...q, locked: true } : q))};
  const handleReset = (qId) => setUserForm(prevForm => prevForm.map(q => q.id === qId ? { ...q, answerKey: [], locked: false } : q));
  const Submit= () => {setedit(false); setUserData(userForm); onSubmit(userForm)}
  const handleNextOrSubmit = () => qNo === userForm.length - 1 ? setShowModal(true) : setQNo(prev => prev + 1);

  return (
    <div className="w-full max-w-5xl bg-white/80 backdrop-blur-md flex-col shadow-lg rounded-2xl p-5 mb-8 flex justify-between items-center border border-white/60">
      <header className="w-full bg-white/80 backdrop-blur-md shadow-lg rounded-2xl p-5 mb-8 flex justify-between items-center border border-white/60">
        <h1 className="text-3xl font-extrabold text-gray-800 tracking-wide">📝 Edit</h1>
        <div className="flex items-center space-x-3">
          <button onClick={() => setShowModal(true)} className="p-2 bg-blue-100 text-white rounded-full hover:bg-blue-200 shadow-md">🙌</button>
          <button onClick={() => setDrawerOpen(!drawerOpen)} className="p-4 bg-white-300 text-white rounded-full hover:bg-white-400 black shadow-md"><span className="text-black"> ☰</span> </button>
        </div>
      </header>

      {drawerOpen && (
        <div className="hehe fixed top-0 right-0 w-80 h-full bg-white shadow-lg p-4 z-50 flex flex-col">
          <div className="flex justify-between mb-4">
            <h2 className="font-bold text-lg">Questions</h2>
            <button onClick={() => setDrawerOpen(false)} className="text-red-500 font-bold">✖</button>
          </div>
          <div className="grid grid-cols-5 gap-2">
            {userForm.map((q, index) => {
              const isAnswered = q.answerKey.length > 0;
              const isMarked = markedQuestions.includes(q.id);
              return (
                <button
                  key={q.id}
                  onClick={() => { setQNo(index); }}
                  className={`transform transition-transform duration-300 ${drawerOpen ? 'translate-y-0' : 'translate-y-full'} shadow	w-10 h-10 flex items-center justify-center rounded-full transform transition-transform duration-300 cursor-pointer text-xs font-bold ${qNo === index ? "bg-purple-400 text-white" : isMarked ? !isAnswered ? "bg-blue-300 text-white" : "bg-green-300" : isAnswered ? "bg-green-300" : "bg-gray-100"}`}
                >
                  {index + 1}
                </button>
              );
            })}
          </div>
        </div>
      )}

      {!showAnalysis && (
        <main className="w-full max-w-5xl space-y-8">
          {userForm.map((q, qIndex) => (
            <div key={q.id} className={`bg-white/90 backdrop-blur-md shadow-lg rounded-2xl p-8 space-y-5 border border-white/50 ${qIndex === qNo ? "block" : "hidden"}`}>
              <div className="flex justify-between items-center">
                <h2 className="text-2xl font-bold text-purple-700">Question {qIndex + 1}</h2>
              </div>
              <He imageUrl={q.img ? q.img !== "" && q.img !== undefined ? q.img : undefined : undefined} input={q.question} maxheight={300}  />
              {q.type === "mcq" && (
                <div className="grid grid-cols-1 gap-4 mt-4">
                  {q.options.map((option, index) => (
                    <button key={index} onClick={() => handleOptionClick(q.id, index)} className={`w-full text-left border rounded-xl p-4 transition-all duration-300 shadow-sm ${q.answerKey.includes(index) ? "bg-green-200 border-green-400" : "bg-gradient-to-r from-gray-50 to-gray-100 hover:from-green-50 hover:to-green-100 hover:border-green-300 border-green-200"}`}>
                                            <He he hein={`${String.fromCharCode(65 + index)})`} input={option} />
                    </button>
                  ))}
                </div>
              )}
              {q.type === "int" && (
                <div className="mt-4 flex gap-2">
                  <input type="number" value={q.answerKey.length ? q.answerKey[0] : ""} disabled={q.locked} onChange={e => handleIntChange(q.id, e.target.value)} className="flex-1 border rounded-xl p-3" placeholder="Enter integer answer" />
                  <button onClick={() => handleIntConfirm(q.id)} disabled={q.locked} className={`px-4 py-2 ${q.locked ? "bg-gray-400" : "bg-green-500 hover:bg-green-600"} text-white rounded-xl`}>
                    ✔
                  </button>
                </div>
              )}
            </div>
          ))}
        </main>
      )}

      {!showAnalysis && (
        <footer className="w-full max-w-5xl flex justify-between mt-8">
          <div className="flex gap-2">
            <button onClick={() => handleReset(userForm[qNo].id)} className="px-6 py-3 bg-yellow-200 text-gray-800 font-semibold rounded-xl hover:bg-yellow-300 shadow-md">Reset</button>
            <button onClick={() => setQNo(prev => Math.max(prev - 1, 0))} className="px-6 py-3 bg-gray-200 text-gray-800 font-semibold rounded-xl hover:bg-gray-300 shadow-md">⬅ Previous</button>
          </div>
          <button onClick={handleNextOrSubmit} className={`px-6 py-3 ${qNo === userForm.length - 1 ? "bg-red-600 hover:bg-red-700" : "bg-purple-600 hover:bg-purple-700"} text-white font-semibold rounded-xl shadow-md`}>
            {qNo === userForm.length - 1 ? "Submit" : "Next ➡"}
          </button>
        </footer>
      )}

      {showModal && (
        <div className="fixed inset-0 bg-black/50 flex justify-center items-center z-50">
          <div className="bg-white rounded-2xl shadow-lg p-6 w-96 space-y-4">
            <h2 className="text-xl font-bold text-gray-800">Are you sure you want to submit?</h2>
            <p className="text-gray-600">You won't be able to change your answers after submission.</p>
            <div className="flex justify-end space-x-4 mt-4">
              <button onClick={() => setShowModal(false)} className="px-4 py-2 bg-gray-300 rounded-xl hover:bg-gray-400">Cancel</button>
              <button onClick={() => { setShowModal(false); Submit(); }} className="px-4 py-2 bg-red-600 text-white rounded-xl hover:bg-red-700">Submit</button>
            </div>
          </div>
        </div>
      )}
    </div>
  );
}
