import React from "react";
import { Link } from "react-router-dom";

export default function NotFound() {
  return (
    <div className="flex flex-col items-center justify-center h-screen bg-gradient-to-br from-gray-900 via-indigo-900 to-purple-900 text-white px-4">
      <h1 className="text-9xl font-extrabold tracking-widest">404</h1>
      
      <div className="bg-indigo-500 px-2 text-sm rounded rotate-12 absolute mt-20">
        Page Not Found
      </div>
      
      <p className="mt-24 text-lg text-gray-300">
        Sorry, the page you are looking for doesn't exist.
      </p>

      <Link
        to="/"
        className="mt-6 px-6 py-3 bg-indigo-500 hover:bg-indigo-600 rounded-lg shadow-lg text-white text-lg font-medium transition-all duration-300 transform hover:scale-105"
      >
        Go Back Home
      </Link>
    </div>
  );
}
