import React from "react";
import { Link } from "react-router-dom";

export default function Guide() {
    const COLORS = {
        correct: "#34D399", // Green
        incorrect: "#F87171", // Red
        unattempted: "#9CA3AF", // Gray
        other: "#A78BFA", // Purple
    };

    return (
        <div className="min-h-screen bg-gradient-to-br from-gray-900 via-gray-800 to-gray-900 p-6">
            <div className="max-w-3xl mx-auto bg-gray-800/90 backdrop-blur-lg rounded-2xl shadow-2xl border border-gray-700 p-8 text-gray-200">
                {/* Header */}
                <div className="flex justify-between items-center mb-6">
                    <h1 className="text-3xl font-extrabold text-gray-100">📖 User Guide</h1>
                    <Link
                        to="/"
                        className="px-4 py-2 bg-gradient-to-r from-blue-500 to-indigo-600 text-white rounded-lg shadow hover:scale-105 transition"
                    >
                        ⬅ Back
                    </Link>
                </div>

                {/* Tutorial Section */}
                <section className="mb-8">
                    <h2 className="text-xl font-bold text-gray-100 mb-3">🚀 How to Use</h2>
                    <ul className="list-disc list-inside space-y-2 text-gray-300">
                        <li>Click <b>Start Test</b> to begin an available test.</li>
                        <li>Answer questions within the given time limit.</li>
                        <li>Your answers are <b>not saved</b> until you click <b>Submit Test</b>.</li>
                        <li>Once submitted, results will appear in the <b>Test Analysis</b> section.</li>
                        <li>Pie charts show your <b>Correct</b>, <b>Incorrect</b>, and <b>Unattempted</b> questions.</li>
                    </ul>

                    {/* JSON Rules Link */}
                    <div className="mt-4 flex justify-center">
                        <Link
                            to="/rules-json"
                            className="inline-flex items-center gap-2 text-sm px-4 py-2 rounded-full bg-gradient-to-r from-purple-500 to-pink-500 text-white font-semibold shadow hover:scale-105 active:scale-95 transition-all"
                        >
                            📜 Rules for JSON
                        </Link>
                    </div>
                </section>

                {/* Colors Section */}
                <section className="mb-8">
                    <h2 className="text-xl font-bold text-gray-100 mb-3">🎨 Color Codes</h2>
                    <div className="grid grid-cols-2 gap-4">
                        <div className="flex items-center gap-3">
                            <div className="w-6 h-6 rounded" style={{ backgroundColor: COLORS.correct }}></div>
                            <span className="text-gray-300">Correct Answer</span>
                        </div>
                        <div className="flex items-center gap-3">
                            <div className="w-6 h-6 rounded" style={{ backgroundColor: COLORS.incorrect }}></div>
                            <span className="text-gray-300">Incorrect Answer</span>
                        </div>
                        <div className="flex items-center gap-3">
                            <div className="w-6 h-6 rounded" style={{ backgroundColor: COLORS.unattempted }}></div>
                            <span className="text-gray-300">Unattempted Question</span>
                        </div>
                        <div className="flex items-center space-x-3">
                            <div className="w-6 h-6 rounded" style={{ backgroundColor: COLORS.other }}></div>
                            <span className="text-gray-300">Other Questions</span>
                        </div>
                    </div>
                </section>

                {/* Important Notes */}
                <section>
                    <h2 className="text-xl font-bold text-yellow-400 mb-3">⚠ Important Notes</h2>
                    <ul className="list-disc list-inside space-y-2 text-gray-300">
                        <li><b>Unsubmitted answers will NOT be included</b> in the downloaded JSON. Always submit the test before exporting results.</li>
                        <li><b>Avoid opening multiple tabs</b> of this app. Running multiple instances can cause local data conflicts or crashes.</li>
                        <li>Refreshing during a test may reset your progress.</li>
                        <li>If the timer runs out, the test will auto-submit.</li>
                        <li>Ensure a stable internet connection for smooth performance.</li>
                    </ul>
                </section>

                {/* How to Safely Restore JSON */}
                <section className="mt-10">
                    <h2 className="text-xl font-bold text-blue-400 mb-3">🛠 How to Safely Restore JSON</h2>
                    <div className="bg-gray-700 border border-blue-500/40 rounded-xl p-5 space-y-4 shadow">
                        <div className="flex items-start space-x-3">
                            <span className="text-green-400 text-lg">✅</span>
                            <p className="text-gray-300">
                                Always download the <span className="font-semibold text-white">latest JSON backup</span> before making any changes.
                            </p>
                        </div>

                        <div className="flex items-start space-x-3">
                            <span className="text-green-400 text-lg">⬆</span>
                            <p className="text-gray-300">
                                Restore data by using the <span className="font-semibold text-white">Upload JSON</span> feature.
                                <span className="text-red-400 font-semibold"> Avoid manual overwrite</span> in <code>localStorage</code>.
                            </p>
                        </div>

                        <div className="flex items-start space-x-3">
                            <span className="text-yellow-400 text-lg">⚠</span>
                            <p className="text-gray-300">
                                Do <span className="font-semibold text-red-400">NOT</span> edit the downloaded JSON file unless you fully understand its format.
                            </p>
                        </div>

                        <div className="flex items-start space-x-3">
                            <span className="text-red-400 text-lg">❌</span>
                            <p className="text-gray-300">
                                Importing an invalid JSON file can cause the app to <span className="font-semibold text-white">crash</span> or 
                                <span className="font-semibold text-white"> erase all saved data</span>.
                            </p>
                        </div>

                        <div className="flex items-start space-x-3">
                            <span className="text-blue-400 text-lg">💡</span>
                            <p className="text-gray-300">
                                If the app misbehaves after restore, clear <code>localStorage</code> and upload a valid backup again.
                            </p>
                        </div>
                    </div>
                </section>

                {/* Caution Section */}
                <section className="mt-8">
                    <h2 className="text-xl font-bold text-red-400 mb-3">🚫 Important Caution</h2>
                    <div className="bg-gray-700 border border-red-500/40 rounded-xl p-4 text-gray-300 space-y-3">
                        <p>
                            Overwriting the app’s <b className="text-white">localStorage</b> manually (through browser developer tools or scripts)
                            can <b className="text-white">delete or corrupt all your saved tests and analysis data</b>.
                            ❗ Please avoid modifying localStorage unless you know exactly what you’re doing.
                        </p>
                        <p>
                            If you think the app is <b className="text-white">crashing</b> or <b className="text-white">not working properly</b> due to manual edits in localStorage,
                            you can <b className="text-white">clear all items from localStorage</b> (via browser settings or console) to reset the app.
                        </p>
                    </div>
                </section>
            </div>
        </div>
    );
}
