import React, { useState, useEffect, useRef } from "react";
import { Link } from "react-router-dom";
import { PieChart, Pie, Cell, Tooltip, Legend } from "recharts";

export default function Lists() {
  const COLORS = ["#34D399", "#F87171", "#D1D5DB", "lightblue"];
  const [open, setOpen] = useState(false);
  const [tests, setTests] = useState([]);
  const [confirmDeleteIndex, setConfirmDeleteIndex] = useState(null);
  const [dialog, setdialog] = useState(false);
  const [activeMenu, setActiveMenu] = useState(null);
  const [downloadDialogStep1, setDownloadDialogStep1] = useState(false);
  const [downloadDialogStep2, setDownloadDialogStep2] = useState(false);

  const menuRef = useRef(null);

  useEffect(() => {
    try {
      const rawData = localStorage.getItem("tests");
      if (rawData) {
        let parsed = JSON.parse(rawData);
        if (!Array.isArray(parsed)) parsed = [];

        const now = new Date();
        let updated = parsed.map(test => {
          let validDate = false;
          if (test.createdAt) {
            const d = new Date(test.createdAt);
            if (!isNaN(d.getTime())) validDate = true;
          }
          return {
            ...test,
            createdAt: validDate ? test.createdAt : now.toISOString(),
          };
        });

        updated.sort((a, b) => new Date(b.createdAt) - new Date(a.createdAt));

        localStorage.setItem("tests", JSON.stringify(updated));
        setTests(updated);
      } else {
        localStorage.setItem("tests", JSON.stringify([]));
        setTests([]);
      }
    } catch {
      localStorage.setItem("tests", JSON.stringify([]));
      setTests([]);
    }
  }, []);

  const deleteTest = (uid) => {
    try {
      const updatedTests = tests.filter(test => test.uid !== uid);
      localStorage.setItem("tests", JSON.stringify(updatedTests));
      setTests(updatedTests);

      const rawTime = localStorage.getItem("time");
      if (rawTime) {
        let parsedTime = JSON.parse(rawTime);
        if (Array.isArray(parsedTime)) {
          parsedTime = parsedTime.filter(entry => entry.id !== uid);
          localStorage.setItem("time", JSON.stringify(parsedTime));
        }
      }
    } catch (err) {
      console.error("❌ Error deleting test:", err);
    }
    setConfirmDeleteIndex(null);
    setdialog(false);
  };

  const downloadJSON = (formatted = false) => {
    try {
      const rawData = localStorage.getItem("tests");
      const parsed = rawData ? JSON.parse(rawData) : [];
      const jsonString = formatted
        ? JSON.stringify(parsed, null, 2)
        : JSON.stringify(parsed);

      const blob = new Blob([jsonString], { type: "application/json" });
      const url = URL.createObjectURL(blob);
      const link = document.createElement("a");
      link.href = url;
      link.download = formatted ? "tests_formatted.json" : "tests_compact.json";
      link.click();
      URL.revokeObjectURL(url);
      setDownloadDialogStep2(false);
    } catch (err) {
      console.error("❌ Error downloading JSON:", err);
    }
  };

  const actions = [
    {
      icon: (
        <Link to={"/create-test"}>
          <div title="Create Test" className="flex items-center justify-center h-full w-full rounded-lg">
            <svg xmlns="http://www.w3.org/2000/svg" className="w-6 h-6" fill="none"
              viewBox="0 0 24 24" stroke="currentColor" strokeWidth={2}>
              <path strokeLinecap="round" strokeLinejoin="round" d="M12 4v16m8-8H4" />
            </svg>
          </div>
        </Link>
      ),
      color: "from-pink-500 to-rose-600",
    },
    {
      icon: (
        <div
          title="Download JSON"
          className="flex items-center justify-center h-full w-full rounded-lg"
          onClick={() => setDownloadDialogStep1(true)}
        >
          <svg xmlns="http://www.w3.org/2000/svg" className="w-6 h-6" fill="none"
            viewBox="0 0 24 24" stroke="currentColor" strokeWidth={2}>
            <path strokeLinecap="round" strokeLinejoin="round"
              d="M4 16v2a2 2 0 002 2h12a2 2 0 002-2v-2M16 10l-4 4m0 0l-4-4m4 4V4" />
          </svg>
        </div>
      ),
      color: "from-cyan-500 to-blue-600",
    },
    {
      icon: (
        <Link to={"/upload-json"}>
          <div title="Upload JSON" className="flex items-center justify-center h-full w-full rounded-lg">
            <svg xmlns="http://www.w3.org/2000/svg" className="w-6 h-6" fill="none"
              viewBox="0 0 24 24" stroke="currentColor" strokeWidth={2}>
              <path strokeLinecap="round" strokeLinejoin="round"
                d="M4 16v2a2 2 0 002 2h12a2 2 0 002-2v-2M16 10l-4-4m0 0l-4 4m4-4v12" />
            </svg>
          </div>
        </Link>
      ),
      color: "from-orange-500 to-red-600",
    },
  ];

  useEffect(() => {
    function handleClickOutside(e) {
      if (menuRef.current && !menuRef.current.contains(e.target)) {
        setActiveMenu(null);
      }
    }
    document.addEventListener("mousedown", handleClickOutside);
    return () => document.removeEventListener("mousedown", handleClickOutside);
  }, []);

  // console.log(chartData)

  return (
    <div className="min-h-screen bg-gradient-to-br from-blue-50 to-purple-100 p-6">
      <div className="flex justify-center items-center gap-2 mb-8">
        <h1 className="text-3xl font-extrabold text-gray-800">
          📚 Available Tests
        </h1>
        <Link
          to="/guide"
          className="flex items-center justify-center w-8 h-8 rounded-full bg-blue-500 hover:bg-blue-600 text-white text-lg shadow transition-transform hover:scale-110"
          title="Guide"
        >
          ℹ
        </Link>
      </div>


      {/* Floating Action Buttons */}
      <div className="hehe fixed bottom-8 right-8 flex items-end justify-end">
        <div className="relative flex flex-col items-center">
          {actions.map((action, i) => {
            const offset = open ? (i + 1) * 70 : 0;
            return (
              <button key={i}
                className={`absolute w-14 h-14 rounded-2xl shadow bg-gradient-to-br ${action.color} text-white backdrop-blur-lg transition-all duration-500 hover:scale-110`}
                style={{
                  transform: `translateY(-${offset}px)`,
                  opacity: open ? 1 : 0,
                  transitionDelay: `${i * 0.01}s`,
                }}>
                {action.icon}
              </button>
            );
          })}
          <button
            onClick={() => setOpen(!open)}
            className={`relative w-16 h-16 rounded-full bg-gradient-to-br from-purple-500 to-indigo-600 text-white shadow backdrop-blur-lg transition-transform ${open ? "rotate-90 scale-110" : "rotate-0 scale-100"}`}
          >
            <div className="flex items-center justify-center h-full w-full">
              <svg xmlns="http://www.w3.org/2000/svg" className="w-6 h-6" fill="none"
                viewBox="0 0 24 24" stroke="currentColor" strokeWidth={2}>
                <path strokeLinecap="round" strokeLinejoin="round" d="M4 6h16M4 12h16M4 18h16" />
              </svg>
            </div>
          </button>
        </div>
      </div>

      {/* Test Cards */}
      {tests.length === 0 ? (
        <div className="w-full max-w-7xl mx-auto py-20">
          <div className="bg-white/70 border border-purple-100 rounded-2xl shadow-lg p-8 flex flex-col items-center justify-center space-y-4">
            <div className="relative">
              <div className="absolute inset-0 bg-purple-300 rounded-full blur-[100px] opacity-20"></div>
              <div className="relative bg-white p-6 rounded-full shadow-lg border border-purple-100 text-4xl">
                📄
              </div>
            </div>
            <h2 className="text-xl font-bold text-gray-700">No Tests Available</h2>
            <p className="text-gray-500 text-sm">Please check back later for new papers.</p>
            <button onClick={() => window.location.reload()}
              className="px-5 py-2 rounded-lg bg-purple-500 hover:bg-purple-600 text-white shadow transition">
              Refresh
            </button>
          </div>
        </div>
      ) : (
        <div className="grid gap-10 grid-cols-1 sm:grid-cols-2 lg:grid-cols-3 max-w-7xl mx-auto">
          {tests.map((test) => {
            if (!Array.isArray(test.questions)) return null;
            let chartData;
            if (test.status !== "true") {

              const correct = test.questions.filter(q =>
                q.type === "int"
                  ? q.chosen[0] === q.answerKey[0]
                  : ([...q.chosen]).sort().join() === ([...q.answerKey]).sort().join()
              ).length;

              const unattempted = test.questions.filter(q => q.chosen.length === 0).length;

              const other = test.questions.filter(q =>
                !Array.isArray(q.answerKey) ||
                q.answerKey.length === 0 ||
                !q.answerKey.every(num => Number.isInteger(num))
              ).length;

              const incorrect = test.questions.length - correct - unattempted - other;

              chartData = [
                { name: "Correct", value: correct },
                { name: "Incorrect", value: incorrect },
                { name: "Unattempted", value: unattempted },
                { name: "Other", value: other },
              ];
            }

            const formattedDate = new Date(test.createdAt).toLocaleDateString("en-GB", {
              day: "2-digit",
              month: "short",
              year: "numeric",
            });

            let timeData = [];
            try {
              const rawTime = localStorage.getItem("time");
              if (rawTime) {
                const parsedTime = JSON.parse(rawTime);
                if (Array.isArray(parsedTime)) {
                  timeData = parsedTime;
                } else {
                  localStorage.setItem("time", JSON.stringify([]));
                  timeData = [];
                }
              }
            } catch {
              localStorage.setItem("time", JSON.stringify([]));
              timeData = [];
            }

            if (test.status === "true" && !timeData.some(entry => entry.id === test.uid)) {
              const durationSeconds = parseInt(test.duration, 10) * 60;
              const mm = String(Math.floor(durationSeconds / 60)).padStart(2, "0");
              const ss = String(durationSeconds % 60).padStart(2, "0");
              timeData.push({ id: test.uid, timeleft: `${mm}:${ss}` });
              localStorage.setItem("time", JSON.stringify(timeData));
            }

            const timeEntry = timeData.find(entry => entry.id === test.uid);
            const timeLeft = timeEntry ? timeEntry.timeleft : "00:00";

            return (
              <div key={test.uid}
                className="relative group rounded-3xl border border-white/30 bg-gradient-to-br from-green-100 via-blue-100 to-purple-100 backdrop-blur-lg shadow p-6 hover:scale-[1.02] transition-all duration-500"
              >
                {/* Date Badge */}
                <div className="relative mb-3 group inline-block">
                  <span className="bg-white/80 text-gray-700 text-xs font-semibold px-3 py-1 rounded-full shadow">
                    📅 {new Date(test.createdAt).toLocaleDateString([], { day: "2-digit", month: "short", year: "numeric" })},
                    {" "}
                    {new Date(test.createdAt).toLocaleTimeString([], { hour: "2-digit", minute: "2-digit" })}
                  </span>
                </div>

                {/* Menu */}
                <div className="absolute top-3 right-3">
                  <button
                    onClick={() => setActiveMenu(activeMenu === test.uid ? null : test.uid)}
                    className="p-2 rounded-full bg-gradient-to-br from-gray-50 via-gray-100 to-gray-200 hover:from-gray-100 hover:via-gray-200 hover:to-gray-300 shadow-md hover:shadow-lg transition-all duration-200 hover:scale-110 active:scale-95"
                  >
                    <svg xmlns="http://www.w3.org/2000/svg"
                      className="w-5 h-5 text-gray-600 hover:text-gray-800 transition-colors"
                      viewBox="0 0 24 24" fill="currentColor">
                      <circle cx="12" cy="5" r="1.5" />
                      <circle cx="12" cy="12" r="1.5" />
                      <circle cx="12" cy="19" r="1.5" />
                    </svg>
                  </button>
                  {activeMenu === test.uid && (
                    <div className="absolute right-0 mt-2 w-40 bg-white border border-gray-200 rounded-2xl shadow-lg z-10">
                      <button
                        onClick={() => { setConfirmDeleteIndex(test.uid); setdialog(true); }}
                        className="w-full px-4 py-2 rounded-xl bg-gradient-to-r from-red-500 to-pink-500 text-white text-sm font-semibold shadow hover:scale-105 active:scale-95 transition-all"
                      >
                        🗑 Delete Test
                      </button>
                    </div>
                  )}
                </div>

                {/* Title */}
                <h2 className="text-2xl font-extrabold text-gray-800 mb-2">{test.title}</h2>
                <p className="text-gray-600 text-sm mb-4">{String(test.description)}</p>

                <div className="flex justify-center mb-3 w-full">
                  {test.status === "true" ? (
                    <div className="w-[200px] h-[130px] mx-auto flex items-center justify-center">
                      <div className="flex w-full rounded-lg overflow-hidden border border-indigo-200 shadow-inner bg-white/40 backdrop-blur-sm">
                        <div className="w-1/2 p-2 flex flex-col justify-center items-center bg-indigo-50">
                          <span className="text-xs text-gray-500">Time Left</span>
                          <span className="text-lg font-bold text-indigo-700">{timeLeft}</span>
                        </div>
                        <div className="w-1/2 p-2 flex flex-col justify-center items-center bg-indigo-100">
                          <span className="text-xs text-gray-500">Duration</span>
                          <span className="text-lg font-bold text-indigo-700">{test.duration}</span>
                        </div>
                      </div>
                    </div>
                  ) : (
                    <div className="flex justify-center items-center w-[130px] h-[130pxpx]">
                      <PieChart width={130} height={130}>
                        <Pie
                          data={chartData}
                          cx="50%"
                          cy="50%"
                          innerRadius={28}
                          outerRadius={65}
                          paddingAngle={2}
                          dataKey="value"
                        >
                          {chartData.map((entry, i) => (
                            <Cell key={i} fill={COLORS[i % COLORS.length]} />
                          ))}
                        </Pie>
                        <Tooltip />
                      </PieChart>
                    </div>


                  )}
                </div>

                <div className="mt-4 flex justify-between items-center">
                  <span className="text-xs font-semibold text-gray-700 tracking-wide">
                    {Array.isArray(test.questions) ? test.questions.length : 0} Questions
                  </span>
                  <Link
                    to={`/test/${test.uid}`}
                    className={`px-5 py-2 text-sm font-semibold rounded-xl active:scale-95  shadow-md transform transition-all duration-300
                      ${test.status === "true"
                        ? (timeLeft === test.duration
                          ? "bg-gradient-to-r from-green-500 to-emerald-600 text-white hover:shadow-lg hover:scale-105"
                          : "bg-gradient-to-r from-yellow-400 to-orange-500 text-white hover:shadow-lg hover:scale-105")
                        : "bg-gradient-to-r from-gray-500 to-gray-700 hover:shadow-lg text-white hover:scale-105"
                      }`}
                  >
                    {test.status === "true"
                      ? (timeLeft === test.duration ? "Start Test" : "Resume Test")
                      : "View More"}
                  </Link>
                </div>
              </div>
            );
          })}
        </div>
      )}

      {/* Confirm Delete Modal */}
      {dialog && (
        <div className="fixed inset-0 bg-black/40 backdrop-blur-sm flex items-center justify-center z-50">
          <div className="bg-gradient-to-br from-white/90 to-gray-100 rounded-2xl p-6 w-96 border">
            <h2 className="text-2xl font-extrabold mb-2">🗑 Delete Test</h2>
            <p className="text-gray-600 text-sm mb-6">
              Are you sure you want to delete this test? This action cannot be undone.
            </p>
            <div className="flex justify-end gap-3">
              <button
                onClick={() => { setdialog(false); setActiveMenu(null) }}
                className="px-5 py-2 transform transition-all duration-200 active:scale-85  rounded-xl bg-gray-200 hover:bg-gray-300"
              >
                Cancel
              </button>
              <button
                onClick={() => deleteTest(confirmDeleteIndex)}
                className="px-5 py-2 transform transition-all duration-200 active:scale-85  rounded-xl bg-gradient-to-r from-red-500 to-pink-500 text-white"
              >
                Delete
              </button>
            </div>
          </div>
        </div>
      )}

      {/* Download JSON Step 1 */}
      {downloadDialogStep1 && (
        <div className="fixed inset-0 bg-black/40 backdrop-blur-sm flex items-center justify-center z-50">
          <div className="bg-gradient-to-br from-white/90 to-gray-100 rounded-2xl p-6 w-96 border">
            <h2 className="text-2xl font-extrabold mb-2">⬇ Download JSON</h2>
            <p className="text-gray-600 text-sm mb-6">
              The JSON will not contain answers for tests that are not submitted. It’s recommended to submit all tests before downloading.
            </p>
            <div className="flex justify-end gap-3">
              <button
                onClick={() => setDownloadDialogStep1(false)}
                className="px-5 py-2 rounded-xl bg-gray-200 hover:bg-gray-300"
              >
                Cancel
              </button>
              <button
                onClick={() => { setDownloadDialogStep1(false); setDownloadDialogStep2(true); }}
                className="px-5 py-2 rounded-xl bg-gradient-to-r from-blue-500 to-indigo-600 text-white"
              >
                Continue
              </button>
            </div>
          </div>
        </div>
      )}

      {/* Download JSON Step 2 */}
      {downloadDialogStep2 && (
        <div className="fixed inset-0 bg-black/40 backdrop-blur-sm flex items-center justify-center z-50">
          <div className="bg-gradient-to-br from-white/90 to-gray-100 rounded-2xl p-6 w-96 border">
            <h2 className="text-xl font-bold mb-4">Select Download Format</h2>
            <p className="text-sm text-red-500 mb-4">⚠ Formatted JSON will be larger in size.</p>
            <div className="flex justify-between gap-3">
              <button
                onClick={() => setDownloadDialogStep2(false)}
                className="px-5 py-2 rounded-xl bg-gray-200 hover:bg-gray-300"
              >
                Cancel
              </button>
              <button
                onClick={() => downloadJSON(true)}
                className="px-5 py-2 rounded-xl bg-gradient-to-r from-green-500 to-emerald-600 text-white"
              >
                Formatted
              </button>
              <button
                onClick={() => downloadJSON(false)}
                className="px-5 py-2 rounded-xl bg-gradient-to-r from-blue-500 to-cyan-600 text-white"
              >
                Compact
              </button>
            </div>
          </div>
        </div>
      )}

    </div>
  );
}
