import { MathJax, MathJaxContext } from 'better-react-mathjax';
import React, { useEffect, useRef } from 'react'

export const He = ({ he, hein, input, left, maxheight, imageUrl }) => {
  const config = {
    loader: { load: ["[tex]/mhchem"] },
    tex: { packages: { "[+]": ["mhchem"] } }
  };

  const ref = useRef(null);

  useEffect(() => {
    if (window.MathJax && ref.current) {
      // Check if using MathJax v3
      if (typeof window.MathJax.typesetPromise === "function") {
        window.MathJax.typesetPromise([ref.current]);
      }
      // Fallback for MathJax v2
      else if (window.MathJax.Hub) {
        window.MathJax.Hub.Queue(["Typeset", window.MathJax.Hub, ref.current]);
      }
    }
  }, [he, input]);



  function RenderParsedContent({ input }) {
    // Parse the input
    const regex = /\[IMG:(.*?)\]/g;
    let lastIndex = 0;
    const parsedContent = [];
    let match;

    while ((match = regex.exec(input)) !== null) {
      const index = match.index;
      const url = match[1];

      // Add text before the image
      if (index > lastIndex) {
        const text = input.slice(lastIndex, index).trim();
        if (text) parsedContent.push({ type: "text", content: text });
      }

      // Add image
      parsedContent.push({ type: "image", url });

      lastIndex = regex.lastIndex;
    }

    // Add remaining text
    if (lastIndex < input.length) {
      const text = input.slice(lastIndex).trim();
      if (text) parsedContent.push({ type: "text", content: text });
    }

    return (
      <>
        {parsedContent.map((item, idx) =>
          item.type === "image" ? (
            <div className={`flex justify-${left == true ? "left" : "center mt-4 "}`}>
              <img key={idx} src={item.url} alt={`img-${idx}`} className="my-2 max-w-full h-auto rounded-lg" />
            </div>
          ) : (
            <MathJax key={idx} dynamic>
              {he ? hein ? hein :"" : ""} {`${item.content}`}
            </MathJax>
          )
        )}
      </>
    );
  }

  return (
    <div ref={ref}>
      <div className={`max-w-full whitespace-normal break-words`}>
        <MathJaxContext config={config}>
          <div className={`question-container p-4 ${left === true ? "flex flex-row" : ""}`} style={{ fontSize: 20 }}>

            {/* Question text */}
            {/* <MathJax dynamic>{`${he ? hein : ""} ${input}`}</MathJax> */}
            {<RenderParsedContent input={input} />}

            {/* Optional Image */}
            {imageUrl !== undefined ? (
              <div className={`flex justify-${left == true ? "left" : "center mt-4 "}`}>
                <img
                  src={imageUrl}
                  alt="Question related"
                  className="max-w-full h-auto rounded-lg "
                  style={{ maxHeight: maxheight }}
                />
              </div>
            ) : ""}

          </div>
        </MathJaxContext>
      </div>
    </div>

  )
}