// ✅ SuccessModal.js
import React, { useEffect } from "react";
import { useNavigate } from "react-router-dom";

export default function SuccessModal({ isOpen, countdown, setCountdown, onClose }) {
  const navigate = useNavigate();

  // Countdown logic
  useEffect(() => {
    if (isOpen && countdown > 0) {
      const timer = setTimeout(() => setCountdown((c) => c - 1), 1000);
      return () => clearTimeout(timer);
    }
    if (isOpen && countdown === 0) {
      navigate("/");
    }
  }, [isOpen, countdown, navigate, setCountdown]);

  if (!isOpen) return null;

  return (
    <div className="fixed inset-0 z-50 flex flex-col items-center justify-center bg-black/60 backdrop-blur-md">
      <div className="bg-gray-900 border border-gray-700 rounded-2xl shadow-2xl w-[400px] p-6 relative text-center">
        <h2 className="text-xl font-bold text-green-400 mb-4">✅ Successfully Updated</h2>
        <p className="text-gray-300 mb-4">
          Redirecting to home in{" "}
          <span className="text-green-400">{countdown}</span> seconds...
        </p>
        <button
          onClick={() => navigate("/")}
          className="mt-2 w-full px-4 py-2 rounded-lg bg-green-600 hover:bg-green-500 text-white font-semibold"
        >
          Go to Home
        </button>
      </div>
    </div>
  );
}
